import React, {useState} from 'react';
import s from './Paginator.module.css';
import cn from 'classnames';
import {Button} from '@material-ui/core'


const Paginator = ({currentPage, onPageChanged, totalItemsCount, pageSize, portionSize = 10}) => {

    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    };


    let portionCount = Math.ceil(pagesCount/portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize +1;
    let rightPortionPageNumber = portionNumber * portionSize;



    return <div className={s.main}>
        { portionNumber > 1 && <Button variant="contained" color="primary"
                                       onClick={ ()=>{ setPortionNumber(portionNumber-1)}}>PREV</Button >}

        { pages
            .filter(p=> p >=leftPortionPageNumber && p<=rightPortionPageNumber)
            .map(p => {
                return <span className={ cn({
                    [s.selectedPage]: currentPage ===p
                }, s.pageNumber)}
                             key={p}
                             onClick={(e) => {
                                 onPageChanged(p);
                                 }}>{p}</span>
                })
        }

        {portionCount > portionNumber &&
                <Button variant="contained" color="primary"
                        onClick={()=>{ setPortionNumber(portionNumber +1)}}>NEXT</Button>}

        </div>
};

export default Paginator;